<template>
<div>
  <Search  @clicked="updateCoord"/>
  <div class="restaurant-container">
    <div class="all_restaurants">
      <h1>All Results</h1>
    </div>
    <GoogleMapLoader
            :allAddresses="allAddresses"
            :allCoords="allCoords"
            :position="position"
    >

    </GoogleMapLoader>
  </div>
</div>
</template>

<script>
    import Search from './Search.vue';
    import RestaurantService from "../RestaurantService";
    import GoogleMapLoader from './GoogleMapLoader.vue'

    export default {
        props: [
          'position'
        ],
        name: "Results",
        components:{
            GoogleMapLoader,
            Search
        },
        data() {
            return {
                address: '',
                allAddresses: [],
                allCoords: [],
                coord: [-118.373253,34.16486]
            }
        },
        methods: {
          updateCoord (coord) {
              console.log(coord)
          }
        },
        computed: {
            mapConfig () {
                return {
                    center: { lat: 0, lng: 0 }
                }
            },
        },
        async created() {
            console.log(this.position)
            try {
                this.restaurants = await RestaurantService.getCloseRestaurants(this.position);
                this.address = this.restaurants[0].address
                this.restaurants.forEach(restaurant => {
                    this.allAddresses.push(restaurant.address);
                    this.allCoords.push(restaurant.position);
                });

            } catch (err) {
                this.error = err.message;
            }
        },
    }
</script>

